.App{
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    overflow: auto;
    background: #373e57;
}

.panel {
    height: 95vh;
    width: 95vw;
    max-width: 1600px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Right {
    height: 95vh;
    width: 19%;
    background: #858cc3;
}

.Centerpanel {
    height: 95vh;
    width: 81%;
    overflow: auto;
    background: #8cced9;
    /* padding: 1.5%; */
}


.Center{
    padding: 1.5%;
}

.Left {
    height: 95vh;
    width: 19%;
    background: #858cc3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.Home , .Projects , .Achievements , .Skills , .About{
    overflow-x: hidden;
}





