::-webkit-scrollbar {
  width: 10px;
  background: transparent;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  border-radius: 20px;
}

::-webkit-scrollbar-thumb {
  cursor: grab;
  background-color: #25629f;
  width: 400px;
  border-radius: 20px;
  border: 1px solid;
  background-clip: content-box;
}

::-webkit-scrollbar-thumb:hover {
  cursor: grab;
  background-color: #2764a1;
  width: 400px;
  border-radius: 20px;
  border: 2px solid #25629f;
  background-clip: content-box;
}
